import { NgIf } from '@angular/common';
import { HttpErrorResponse } from '@angular/common/http';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  Input,
  OnInit,
  inject,
} from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import {
  IProduct,
  IProductProperties,
  TYPE_FORM_CONTROL,
} from '@icom/interfaces';
import { IComMessageService } from '@icom/services/common';
import { IComLibsServicesProductService } from '@icom/services/projects';
import {
  FormlyFieldConfig,
  FormlyFormOptions,
  FormlyModule,
} from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { cloneDeep } from 'lodash';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { lastValueFrom, map } from 'rxjs';
import { IcomLibsProductDetailHistoryComponent } from '../history/history.component';
import { NzSpinModule } from 'ng-zorro-antd/spin';
import { IcomLibsHasPermissionDirective } from '@icom/directive';

@Component({
  selector: 'icom-libs-product-detail-properties',
  templateUrl: './properties.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgIf,
    IcomLibsProductDetailHistoryComponent,
    NzSpinModule,
    IcomLibsHasPermissionDirective
  ],
  changeDetection: ChangeDetectionStrategy.Default,
})
export class IcomLibsProductDetailPropertyComponent implements OnInit {
  protected form: FormGroup = new FormGroup({});
  protected model: IProductProperties;
  protected modelOrigin!: IProductProperties;
  protected fields: FormlyFieldConfig[] = [];
  protected isLoading!: boolean;
  protected options: FormlyFormOptions = {
    formState: {
      disabled: true,
    },
  };
  protected loadingIndicator!: boolean;

  @Input() product: IProduct | undefined;

  protected productService = inject(IComLibsServicesProductService);
  protected IComMessageService = inject(IComMessageService);
  protected changeDetectorRef = inject(ChangeDetectorRef);

  constructor() {
    this.model = {};
    this.fields = [
      {
        key: 'voucher_type',
        type: 'select-custom',
        templateOptions: {
          label: 'Voucher Type',
          placeholder: 'Select Voucher Type',
          classInclude: 'w-[300px]',
          viewValue: 'name',
          labelProp: 'name',
          valueProp: 'code',
          required: true,

          options: this.productService.getOptionType('VOUCHER_TYPE').pipe(
            map(result => {
              return result?.data;
            })
          ),
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },
      },
      {
        key: 'voucher_value',
        type: 'input',
        className: '',
        expressions: {
          'props.disabled': 'formState.disabled',
        },
        props: {
          label: 'Voucher Value',
          type: 'number',
          min: 0
        },
      },
      {
        key: 'expiry_policy',
        className: 'basis-4/12',
        type: 'select-custom',
        props: {
          placeholder: 'Select Expiry policy',
          label: 'Expiry policy',
          viewValue: 'name',
          classInclude: 'w-[300px]',
          labelProp: 'name',
          valueProp: 'code',
          required: true,
          options: this.productService
            .getOptionType('VOUCHER_PROPERTY_EXPIRY_POLICY')
            .pipe(
              map((result) => {
                return result?.data;
              })
            ),
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },
      },
      {
        key: 'expiry_date',
        type: 'date',
        props: {
          label: 'Expiry date',
          ShowTime: true,
          required: true,
          format: 'dd-MM-yyyy HH:mm',
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },
        hideExpression: (field: IProductProperties) => {
          return (field?.expiry_policy) ? (field?.expiry_policy !== 'EXPIRY_DATE_FP') : true;
        },
      },
      {
        key: 'expiry_param',
        type: 'input',
        className: 'basis-6/12 ml-4',
        props: {
          label: 'Expiry param',
          required: true,
          type: 'number',
        },
        hideExpression: (field: IProductProperties) => {
          if (!field?.expiry_policy) {
            return true;
          }
          return field?.expiry_policy === 'EXPIRY_DATE_NE' || field?.expiry_policy === 'EXPIRY_DATE_SDED' || field?.expiry_policy === 'EXPIRY_DATE_FP';
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },
      },
    ];
  }

  async ngOnInit() {
    try {
      this.loadingIndicator = true;
      this.model = await lastValueFrom(
        this.productService.getProductProperties(this.product?.id)
      );
      this.modelOrigin = cloneDeep(this.model);
    } catch (err) {
      this.IComMessageService.showAlertMessageError(
        (err as HttpErrorResponse)?.error?.error_message
      );
    } finally {
      this.loadingIndicator = false;
      this.changeDetectorRef.detectChanges();
    }
  }

  protected async handlerSave(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    try {
      if (!this.form.valid) {
        Object.keys(this.form.controls as TYPE_FORM_CONTROL).forEach((key) => {
          (this.form.controls as TYPE_FORM_CONTROL)[key].touched = true;
        });
        return;
      }
      this.form.markAsTouched();
      (this.form as TYPE_FORM_CONTROL).submitted = true;
      const model = {
        ...this.model,
      };
      this.isLoading = true;
      this.changeDetectorRef.detectChanges();
      if (model.id) {
        await lastValueFrom(this.productService.updateProductProperties(model));
        // this.isLoading = false;
        // this.changeDetectorRef.detectChanges();
        this.toggleDisabled();
        this.IComMessageService.showAlertMessageDone('Edit product properties success');
        this.isLoading = false;
        this.changeDetectorRef.detectChanges();
        return;
      }
      delete model.id;
      await lastValueFrom(this.productService.createProductProperties(model));
      this.isLoading = false;
      this.changeDetectorRef.detectChanges();
      this.IComMessageService.showAlertMessageDone(
        'Add product properties success'
      );
    } catch (err) {
      this.IComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message);
    } finally {
      this.isLoading = false;
      this.changeDetectorRef.detectChanges();
    }
  }

  protected handlerCancel(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    this.toggleDisabled();
    this.form.reset(this.modelOrigin);
  }

  protected toggleDisabled() {
    this.options.formState.disabled = !this.options.formState.disabled;
  }
}
