/* eslint-disable @typescript-eslint/no-explicit-any */
import { Component, Input, OnInit } from '@angular/core';
import * as dayjs from 'dayjs';
import { NzCardModule } from 'ng-zorro-antd/card';
import { NzDividerModule } from 'ng-zorro-antd/divider';

@Component({
  // eslint-disable-next-line @angular-eslint/component-selector
  selector: 'icom-libs-product-detail-history',
  templateUrl: './history.component.html',
  standalone: true,
  imports: [NzCardModule, NzDividerModule]
})
export class IcomLibsProductDetailHistoryComponent implements OnInit {
  @Input() product: any;

  ngOnInit(): void {
    console.log(this.product)

    if (!this.product.created_at) {
      return;
    }
    this.product.created_at_format = dayjs(this.product.created_at).format('DD/MM/YYYY HH:mm');
    this.product.updated_at_format = dayjs(this.product.updated_at).format('DD/MM/YYYY HH:mm');
  }
}
