import { NgIf } from '@angular/common';
import { HttpErrorResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, Input, OnInit, inject } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { parseDateRangeToTwoKey } from '@icom/defines';
import { IProduct, IProductDiscount, IProductPrice, TYPE_FORM_CONTROL } from '@icom/interfaces';
import { IComMessageService } from '@icom/services/common';
import { IComLibsServicesProductService } from '@icom/services/projects';
import { FormlyFieldConfig, FormlyFormOptions, FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import * as dayjs from 'dayjs';
import { cloneDeep } from 'lodash';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { lastValueFrom, map } from 'rxjs';
import { IcomLibsProductDetailHistoryComponent } from '../history/history.component';
import { NzSpinModule } from 'ng-zorro-antd/spin';
import { IcomLibsHasPermissionDirective } from '@icom/directive';

@Component({
  selector: 'icom-libs-product-detail-discount',
  templateUrl: './discount.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgIf,
    IcomLibsProductDetailHistoryComponent,
    NzSpinModule,
    IcomLibsHasPermissionDirective
  ]
})
export class IcomLibsProductDetailDiscountComponent implements OnInit {
  protected form = new FormGroup({});
  protected model: IProductDiscount;
  protected options: FormlyFormOptions = {
    formState: {
      disabled: true,
    },
  };
  protected fields: FormlyFieldConfig[] = [];
  protected modelOrigin!: IProductDiscount;
  protected isLoading!: boolean;
  protected loadingIndicator!: boolean;

  @Input() product: IProduct | undefined;

  protected productService = inject(IComLibsServicesProductService)
  protected IComMessageService = inject(IComMessageService);
  protected cdr = inject(ChangeDetectorRef);

  constructor() {
    this.model = {};
    this.fields = [

      {
        validators: {
          validation: [{ name: 'fieldMatch', options: { errorPath: 'value' } }],
        },
        fieldGroup: [
          {
            key: 'type',
            type: 'select-custom',
            props: {
              label: 'Payment Type',
              placeholder: 'Select Type',
              viewValue: 'name',
              classInclude: 'w-[300px]',
              labelProp: 'name',
              valueProp: 'code',
              required: true,
              options: this.productService.getOptionType('PRODUCT_DISCOUNT_TYPE').pipe(
                map(result => {
                  return result?.data;
                }
                ))
            },
            expressions: {
              'props.disabled': 'formState.disabled',
            },
          }
        ]

      },
      {
        key: 'value',
        type: 'input',
        className: '',
        props: {
          label: 'Value',
          required: true,
          type: 'number'
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },

      },
      {
        key: 'status',
        className: 'basis-4/12',
        type: 'Status',
        props: {
          placeholder: 'Select Status',
          label: 'Pool',
          viewValue: 'name',
          labelProp: 'name',
          valueProp: 'id',
          required: true,
          options: this.productService.getOptionType('PRODUCT_DISCOUNT_STATUS').pipe(
            map(result => {
              return result?.data;
            }
            ))
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },
      },
      {
        key: 'date_range',
        type: 'icom-range-picker',
        props: {
          required: true,
          className: 'mt-4',
          label: 'Valid',
          nzShowTime: true,
          format: 'dd-MM-yyyy HH:mm',
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },
      },
      {
        key: 'minimum_order_value',
        type: 'input',
        className: '',
        props: {
          label: 'Minimum order value',
          required: true,
          type: 'number'
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },
      },
      {
        key: 'maximum_discount_amount',
        type: 'input',
        className: '',
        props: {
          label: 'Maximum discount amount',
          required: true,
          type: 'number'
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },
      },
    ];
  }

  async ngOnInit() {
    try {
      this.loadingIndicator = true;
      this.cdr.detectChanges();
      this.model = await lastValueFrom(this.productService.getProductDiscount(this.product?.id));
      this.modelOrigin = cloneDeep(this.model);
      if (!this.model.valid_from || !this.model.valid_until) {
        return;
      }
      this.model.date_range = [dayjs(this.model.valid_from).toDate(), dayjs(this.model.valid_until).toDate()];
    } catch (err) {
      this.IComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message);
    } finally {
      this.loadingIndicator = false;
      this.cdr.detectChanges();
    }
  }

  protected async handlerSave(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    if (!this.form.valid) {
      Object.keys(this.form.controls as TYPE_FORM_CONTROL).forEach((key) => {
        (this.form.controls as TYPE_FORM_CONTROL)[key].touched = true;
      });
      return;
    }
    try {
      this.form.markAsTouched();
      (this.form as TYPE_FORM_CONTROL).submitted = true;
      const model = {
        ...this.model
      };
      this.isLoading = true;
      this.cdr.detectChanges();
      parseDateRangeToTwoKey(model, 'valid_from,valid_until');
      if (model.id) {
        await lastValueFrom(this.productService.updateProductDiscount(model))
        this.IComMessageService.showAlertMessageDone('Edit product discount success');
        this.toggleDisabled();
        return;
      }
      delete model.id
      await lastValueFrom(this.productService.createProductDiscount(model));
      this.IComMessageService.showAlertMessageError('Edit product discount success');
    } catch (err) {
      this.IComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message);
    } finally {
      this.isLoading = false;
      this.cdr.detectChanges();
    }
  }

  protected handlerCancel(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    this.toggleDisabled();
    this.form.reset(this.modelOrigin);
  }

  protected toggleDisabled() {
    this.options.formState.disabled = !this.options.formState.disabled;
  }
}
