import { Component, Input, OnInit, inject } from '@angular/core';
import { IProduct } from '@icom/interfaces';
import { IComLibsServicesProductService } from '@icom/services/projects';
import { IcomLibsProductAddEditComponent } from '@icom/product';
import * as dayjs from 'dayjs';
import { IcomLibsProductDetailHistoryComponent } from '../history/history.component';
import { NgIf } from '@angular/common';

@Component({
  selector: 'icom-libs-product-detail-general',
  templateUrl: './general.component.html',
  standalone: true,
  imports: [
    IcomLibsProductAddEditComponent,
    IcomLibsProductDetailHistoryComponent,
    NgIf
  ]
})
export class IcomLibsProductDetailGeneralComponent implements OnInit {
  
  @Input() product: IProduct | undefined;

  protected productService = inject(IComLibsServicesProductService)
  
  ngOnInit() {
    if(!this.product) {
      return;
    }
    this.product.date_range = [dayjs(this.product.start_time).toDate(), dayjs(this.product.end_time).toDate()];  
  }

}
