import { TemplateRef } from "@angular/core"
import { TYPE_TEMPLATE_REF } from "@icom/interfaces"
import { FormlyFieldConfig } from "@ngx-formly/core";

export const ConfigColumnLanguage = (edit: TemplateRef<TYPE_TEMPLATE_REF>, remove: TemplateRef<TYPE_TEMPLATE_REF>) => {
  return [
    { prop: '',  cellTemplate: edit, sortable: false, width: 40 },
    { prop: '',  cellTemplate: remove, sortable: false, width: 40 },
    { prop: 'language', width: 200, sortable: false  },
    { prop: 'name', width: 300, sortable: false  },
    { prop: 'description', width: 300, sortable: false  },

  ]
};


export const ConfigFormly  = (): FormlyFieldConfig[]  => [
  {
    key: 'name',
    type: 'input',
    props: {
      label: 'Name',
      placeholder: 'Enter Name',
      type: 'string',
      required: true
    },
  },
  {
    key: 'description',
    type: 'input',
    props: {
      label: 'Description',
      placeholder: 'Enter Description',
      type: 'string',
      required: true
    },
  },
  {
    key: 'language',
    type: 'select-custom',
    templateOptions: {
      label: 'Language',
      placeholder: 'Select Language',
      viewValue: 'name',
      labelProp: 'name',
      required: true,
      valueProp: 'code',
      options: [
        { code: 'vi', name: 'Vietnamese' },
		    { code: 'en', name: 'English' }
      ]
    },
  },
  {
    key: 'detail',
    type: 'quill-editor',
    props: {
      label: 'Detail',
      placeholder: 'Enter Content',
      type: 'string',
      required: true
    },
  },
  {
    key: 'term_and_condition',
    type: 'quill-editor',
    props: {
      label: 'Term and condition',
      placeholder: 'Enter Content',
      type: 'string',
      required: true
    },
  },
];