import { TemplateRef } from "@angular/core"
import { TYPE_TEMPLATE_REF } from "@icom/interfaces"
import { IComLibsServicesProductService } from "@icom/services/projects";
import { FormlyFieldConfig } from "@ngx-formly/core";
import { map } from "rxjs";

export const ConfigColumnMedia = (edit: TemplateRef<TYPE_TEMPLATE_REF>, remove: TemplateRef<TYPE_TEMPLATE_REF>, avatarRef: TemplateRef<TYPE_TEMPLATE_REF> ) => {
  return [
    { prop: '',  cellTemplate: edit, sortable: false, width: 40 },
    { prop: '',  cellTemplate: remove, sortable: false, width: 40 },
    { prop: 'banner', sortable: false , cellTemplate: avatarRef, width: 200 },
    { prop: 'name', sortable: false , width: 300  },
    { prop: 'type', sortable: false , width: 300  },
  ]
};


export const ConfigFormlyMedia  = (productService: IComLibsServicesProductService): FormlyFieldConfig[]  => [
  {
    key: 'name',
    type: 'input',
    props: {
      label: 'Name',
      placeholder: 'Enter Name',
      type: 'string',
      required: true
    },
  },
  {
    key: 'type',
    type: 'select-custom',
    templateOptions: {
      label: 'Type',
      placeholder: 'Select Type',
      viewValue: 'name',
      labelProp: 'name',
      required: true,
      valueProp: 'code',
      classInclude: 'w-[300px]',
      options: productService.getOptionType('MEDIA_TYPE').pipe(
        map(result => {
          return result?.data;
        }
        ))
    },
  }
]