/* eslint-disable @typescript-eslint/no-explicit-any */
import {
  ChangeDetectorRef,
  Component,
  Input,
  OnInit,
  TemplateRef,
  ViewChild,
  inject,
} from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import {
  IConfigTable,
  IProduct,
  IProductLanguage,
  IProductMedia,
  TYPE_FORM_CONTROL,
  TYPE_TEMPLATE_REF,
} from '@icom/interfaces';
import { IComLibsServicesProductService } from '@icom/services/projects';
import {
  FormlyFieldConfig,
  FormlyFormOptions,
  FormlyModule,
} from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { Observable, Observer, lastValueFrom, map } from 'rxjs';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { JsonPipe, NgIf, NgOptimizedImage } from '@angular/common';
import { NzIconModule } from 'ng-zorro-antd/icon';
import { NzModalService } from 'ng-zorro-antd/modal';
import { IComMessageService } from '@icom/services/common';
import {
  HttpErrorResponse,
} from '@angular/common/http';
import { ConfigColumnMedia, ConfigFormlyMedia } from '../defines/media.define';
import { NzUploadFile, NzUploadModule } from 'ng-zorro-antd/upload';
import { NzMessageService } from 'ng-zorro-antd/message';
import { IcomLibsProductDetailHistoryComponent } from '../history/history.component';
import { NzSpinModule } from 'ng-zorro-antd/spin';
import { IcomLibsHasPermissionDirective } from '@icom/directive';
@Component({
  selector: 'icom-libs-product-detail-media',
  templateUrl: './media.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgxDatatableModule,
    NgIf,
    NzIconModule,
    NgOptimizedImage,
    NzUploadModule,
    IcomLibsProductDetailHistoryComponent,
    JsonPipe,
    NzSpinModule,
    IcomLibsHasPermissionDirective
  ],
})
export class IcomLibsProductDetailMediaComponent implements OnInit {
  protected form = new FormGroup({});
  protected options: FormlyFormOptions = {};
  protected modelForm: IProductMedia;
  protected isLoading!: boolean;
  protected configTable!: IConfigTable;
  protected loadingIndicator!: boolean;
  protected isAddNew!: boolean;
  protected loading!: boolean;
  protected fileList: NzUploadFile[] = [];

  @Input() fields: FormlyFieldConfig[] = [];
  @Input() rows: IProductLanguage[] | undefined;
  @Input() product: IProduct | undefined;

  protected productService = inject(IComLibsServicesProductService);
  protected modalService = inject(NzModalService);
  protected IComMessageService = inject(IComMessageService);
  protected cdr = inject(ChangeDetectorRef)

  @ViewChild('statusRef', { static: true })
  statusRef!: TemplateRef<TYPE_TEMPLATE_REF>;
  @ViewChild('removeRef', { static: true })
  removeRef!: TemplateRef<TYPE_TEMPLATE_REF>;
  @ViewChild('avatarRef', { static: true })
  avatarRef!: TemplateRef<TYPE_TEMPLATE_REF>;

  constructor(
    private msg: NzMessageService,
  ) {
    this.modelForm = {};
  }
  uploading!: boolean;
  ngOnInit() {
    this.fields = ConfigFormlyMedia(this.productService);
    this.configTable = {
      loadingIndicator: true,
      columns: ConfigColumnMedia(
        this.statusRef,
        this.removeRef,
        this.avatarRef
      ),
      headerHeight: 50,
      rowHeight: 'auto',
      pageLimit: 10,
    };
    this.loadPage();
  }

  protected async loadPage() {
    try {
      this.loadingIndicator = true;
      const result = await lastValueFrom(
        this.productService.getProductMedia(this.product?.id || '')
      );

      if (result && result.data && result.data) {
        const rows = [...(result.data.items || [])];
        this.rows = rows;
      }
    } catch (err) {
      console.log(err);
    } finally {
      this.loadingIndicator = false;
      this.cdr.detectChanges();
    }
  }

  protected handlerAdd() {
    this.isAddNew = !this.isAddNew;
    this.modelForm = {};
    this.cdr.detectChanges();
  }

  protected handlerCancel() {
    this.isAddNew = false;
    this.modelForm = {};
    this.fileList = [];
    this.cdr.detectChanges();
  }

  protected async handlerSave() {
    try {
      if (!this.form.valid) {
        Object.keys(this.form.controls as TYPE_FORM_CONTROL).forEach((key) => {
          (this.form.controls as TYPE_FORM_CONTROL)[key].touched = true;
        });
        return;
      }

      this.form.markAsTouched();
      (this.form as TYPE_FORM_CONTROL).submitted = true;
      this.processUpload();
    } catch (err) {
      this.IComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message || 'Upload Media Failed');
    } finally {
      this.isLoading = false;
      this.cdr.detectChanges();
    }
  }

  private async processUpload() {
    const formData = new FormData();
    this.fileList[0]?.originFileObj && formData.append('image', this.fileList[0].originFileObj as File);
    const model = {
      product_id: this.product?.id,
      ...this.modelForm,
    };
    this.isLoading = true;
    this.cdr.detectChanges();
    let res: any = null;
    try {
      this.fileList[0]?.originFileObj && (res = await lastValueFrom(this.productService.uploadMediaProduct(formData)));
      if (!model.id) {
        delete model.id;
        model.url = res.data?.path || '';
        await lastValueFrom(
          this.productService.addMediaProduct(model)
        );
        this.IComMessageService.showAlertMessageDone('Add media success');
        this.handlerCancel();
        this.loadPage();

        return;
      }
      model.url = res?.data?.path || model?.url;
      await lastValueFrom(
        this.productService.updateMediaProduct(model)
      );
      this.IComMessageService.showAlertMessageDone('Edit media success');
      this.handlerCancel();
      this.loadPage();
    } catch (err) {
      this.IComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message || 'Upload media fail');
    }
  }

  protected handlerEdit(e: Event, row: IProductLanguage) {
    e.stopPropagation();
    this.modelForm = row;
    this.fileList = [
      {
        uid: '-1',
        name: 'Img',
        status: 'done',
        url: this.modelForm.url,
        thumbUrl: this.modelForm.url,
      },
    ];
    this.isAddNew = true;
    this.cdr.detectChanges();
  }

  protected handlerRemove(e: Event, row: IProductLanguage) {
    e.stopPropagation();
    this.modalService.confirm(
      {
        nzTitle: 'Are you sure delete this media?',
        nzContent: `<b style="color: red;">${row.name}</b>`,
        nzOkText: 'Delete',
        nzOkType: 'primary',
        nzOkDanger: true,
        nzMaskClosable: false,
        nzClosable: false,
        nzOnOk: this.processDelete.bind(this, row),
        nzCancelText: 'Cancel',
      },
      'error'
    );
  }

  private processDelete(row: IProductLanguage) {
    return lastValueFrom(
      this.productService.deleteProductMedia(row.id || '')
    )
      .then(() => {
        this.IComMessageService.showAlertMessageDone('Delete media success');
        this.loadPage();
      })
      .catch((err) => {
        this.IComMessageService.showAlertMessageError(
          (err as HttpErrorResponse)?.error?.error_message || 'Delete Fail'
        );
      });
  }



  protected beforeUpload = (file: NzUploadFile): Observable<boolean> =>
    new Observable((observer: Observer<boolean>) => {
      const isJpgOrPng =
        file.type === 'image/jpeg' || file.type === 'image/png';
      if (!isJpgOrPng) {
        this.msg.error('You can only upload JPG file!');
        observer.complete();
        return;
      }
      const isLt2M = (file?.size || 1) / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.msg.error('Image must smaller than 2MB!');
        observer.complete();
        return;
      }
      if (this.fileList && this.fileList.length) {
        this.msg.error('Only Post One File');
        observer.complete();
        return;
      }
      observer.next(isJpgOrPng && isLt2M);
      observer.complete();
    });
}
