import { NgIf } from '@angular/common';
import { HttpErrorResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, Input, OnInit, inject } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { IProduct, IProductPrice, TYPE_FORM_CONTROL } from '@icom/interfaces';
import { IComMessageService } from '@icom/services/common';
import { IComLibsServicesProductService } from '@icom/services/projects';
import { FormlyFieldConfig, FormlyFormOptions, FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { cloneDeep } from 'lodash';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { lastValueFrom, map } from 'rxjs';
import { IcomLibsProductDetailHistoryComponent } from '../history/history.component';
import { NzSpinModule } from 'ng-zorro-antd/spin';
import { IcomLibsHasPermissionDirective } from '@icom/directive';

@Component({
  selector: 'icom-libs-product-detail-price',
  templateUrl: './price.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgIf,
    IcomLibsProductDetailHistoryComponent,
    NzSpinModule,
    IcomLibsHasPermissionDirective
  ]
})
export class IcomLibsProductDetailPriceComponent implements OnInit {
  protected modelOrigin!: IProductPrice;

  protected form = new FormGroup({});
  protected model: IProductPrice;
  protected options: FormlyFormOptions = {
    formState: {
      disabled: true,
    },
  };
  protected isLoading!: boolean;
  protected fields: FormlyFieldConfig[] = [];
  protected loadingIndicator!: boolean;

  @Input() product: IProduct | undefined;

  protected productService = inject(IComLibsServicesProductService)
  protected IComMessageService = inject(IComMessageService);
  protected cdr = inject(ChangeDetectorRef);

  constructor() {
    this.model = {
      subpool_id: 0,
      pool_id: 8
    };
    this.fields = [

      {
        key: 'payment_method',
        type: 'select-custom',
        props: {
          label: 'Payment Method',
          classInclude: 'w-[300px]',
          placeholder: 'Select Payment Method',
          viewValue: 'name',
          labelProp: 'name',
          valueProp: 'code',
          required: true,
          options: this.productService.getOptionType('PAYMENT_METHOD').pipe(
            map(result => {
              return result?.data;
            }
            ))
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },
      },
      {
        key: 'sale_price',
        type: 'input',
        className: '',
        props: {
          label: 'Sale price',
          required: true,
          type: 'number'
        },
        validators: [
          'iniMin0'
        ],
        expressions: {
          'props.disabled': 'formState.disabled',
        },
      },
      {
        key: 'purchase_cost',
        type: 'input',
        className: 'basis-6/12 ml-4',
        props: {
          label: 'Purchase cost',
          required: true,
          type: 'number'
        },
        validators: [
          'iniMin0'
        ],
        expressions: {
          'props.disabled': 'formState.disabled',
        },
      },
      {
        key: 'pool_id',
        className: 'basis-4/12',
        type: 'select-custom',
        props: {
          placeholder: 'Select Pool',
          label: 'Pool',
          viewValue: 'id',
          labelProp: 'id',
          valueProp: 'id',
          classInclude: 'w-[300px]',
          required: true,
          options: [
            { id: 8, name: 'Pool' },
          ]
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },
      },
      {
        key: 'subpool_id',
        type: 'select-custom',
        className: 'basis-4/12 ml-4',
        props: {
          label: 'Sub Pool',
          placeholder: 'Select Sub Pool',
          viewValue: 'id',
          required: true,
          classInclude: 'w-[300px]',
          labelProp: 'id',
          valueProp: 'id',
          options: [
            { id: 0, name: 'Sub Pool' },
          ]
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },
      },
    ];
  }

  async ngOnInit() {
    try {
      this.loadingIndicator = true;
      this.model = await lastValueFrom(this.productService.getProductPrice(this.product?.id));
      this.modelOrigin = cloneDeep(this.model);
    } catch (err) {
      this.IComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message);
    }
    finally {
      this.loadingIndicator = false;
      this.cdr.detectChanges();
    }
  }

  protected async handlerSave(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    try {
      if (!this.form.valid) {
        Object.keys(this.form.controls as TYPE_FORM_CONTROL).forEach((key) => {
          (this.form.controls as TYPE_FORM_CONTROL)[key].touched = true;
        });
        return;
      }
      this.isLoading = true;
      this.cdr.detectChanges();
      this.form.markAsTouched();
      (this.form as TYPE_FORM_CONTROL).submitted = true;
      const model: IProductPrice = {
        ...this.model,
      }
      if (model.id) {
        await lastValueFrom(this.productService.updateProductPrice(model));
        this.IComMessageService.showAlertMessageDone('Edit product price success');
        this.modelOrigin = cloneDeep(this.model);
        this.toggleDisabled();
        return;

      }
      await lastValueFrom(this.productService.createProductPrice(model));
      this.modelOrigin = cloneDeep(this.model);
      this.IComMessageService.showAlertMessageError('Edit product price success');
    } catch (err) {
      this.IComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message);
    } finally {
      this.isLoading = false;
      this.cdr.detectChanges();
    }
  }

  protected handlerCancel(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    this.toggleDisabled();
    this.form.reset(this.modelOrigin);
  }

  protected toggleDisabled() {
    this.options.formState.disabled = !this.options.formState.disabled;
  }
}
