import { ChangeDetectorRef, Component, Input, OnInit, TemplateRef, ViewChild, inject } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { Router } from '@angular/router';
import { IConfigTable, IProduct, IProductLanguage, TYPE_FORM_CONTROL, TYPE_TEMPLATE_REF } from '@icom/interfaces';
import { IComLibsServicesProductService } from '@icom/services/projects';
import { FormlyFieldConfig, FormlyFormOptions, FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { lastValueFrom } from 'rxjs';
import { ConfigColumnLanguage, ConfigFormly } from '../defines/language.define';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { KeyValuePipe, NgIf } from '@angular/common';
import { NzIconModule } from 'ng-zorro-antd/icon';
import { NzModalService } from 'ng-zorro-antd/modal';
import { IComMessageService } from '@icom/services/common';
import { HttpErrorResponse } from '@angular/common/http';
import { IcomLibsProductDetailHistoryComponent } from '../history/history.component';
import { NzSpinModule } from 'ng-zorro-antd/spin';
import { IcomLibsHasPermissionDirective } from '@icom/directive';
@Component({
  selector: 'icom-libs-product-detail-language',
  templateUrl: './language.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgxDatatableModule,
    NgIf,
    NzIconModule,
    IcomLibsProductDetailHistoryComponent,
    KeyValuePipe,
    NzSpinModule,
    IcomLibsHasPermissionDirective
  ]
})
export class IcomLibsProductDetailLanguageComponent implements OnInit {
  protected form = new FormGroup({});
  protected options: FormlyFormOptions = {};
  protected modelForm: IProductLanguage;
  protected isLoading!: boolean;
  protected configTable!: IConfigTable;
  protected loadingIndicator!: boolean;
  protected isAddNew!: boolean;

  @Input() fields: FormlyFieldConfig[] = [];
  @Input() rows: IProductLanguage[] | undefined;
  @Input() product: IProduct | undefined;

  protected productService = inject(IComLibsServicesProductService);
  protected modalService = inject(NzModalService);
  protected IComMessageService = inject(IComMessageService);
  protected cdr = inject(ChangeDetectorRef);

  @ViewChild('statusRef', { static: true }) statusRef!: TemplateRef<TYPE_TEMPLATE_REF>;
  @ViewChild('removeRef', { static: true }) removeRef!: TemplateRef<TYPE_TEMPLATE_REF>;

  constructor(
    private router: Router,
  ) {
    this.modelForm = {};
  }

  ngOnInit() {
    this.fields = ConfigFormly();
    this.configTable = {
      loadingIndicator: true,
      columns: ConfigColumnLanguage(this.statusRef, this.removeRef),
      headerHeight: 50,
      rowHeight: 'auto',
      pageLimit: 10
    }
    this.loadPage();
  }

  protected async loadPage() {
    try {
      this.loadingIndicator = true;
      const result = await lastValueFrom(this.productService.getProductLanguage(this.product?.id || ''));

      if (result && result.data && result.data) {
        const rows = [...result.data.items || []];
        this.rows = rows;
      }
    } catch (err) {
      console.log(err);
    } finally {
      this.loadingIndicator = false;
      this.cdr.detectChanges();
    }
  }

  protected handlerAdd() {
    this.isAddNew = !this.isAddNew;
  }

  protected handlerCancel() {
    this.isAddNew = false;
    this.modelForm = {};
  }

  protected async handlerSave(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    try {
      if (!this.form.valid) {
        Object.keys(this.form.controls as TYPE_FORM_CONTROL).forEach((key) => {
          (this.form.controls as TYPE_FORM_CONTROL)[key].touched = true;
        });
        return;
      }
      this.form.markAsTouched();
      (this.form as TYPE_FORM_CONTROL).submitted = true;
      const model: IProductLanguage = {
        product_id: this.product?.id,
        ...this.modelForm
      }
      this.isLoading = true;
      this.cdr.detectChanges();
      if (!model.id) {
        delete model.id;
        const existData = this.rows?.some(x => x.language == model.language);
        if (existData) {
          this.IComMessageService.showAlertMessageError('Record exist in table');
          return;
        }
        this.productService.addLanguageProduct(model).subscribe(() => {
          this.IComMessageService.showAlertMessageDone('Add language success');
          this.handlerCancel();
          this.loadPage();
        });

        return;
      }

      await lastValueFrom(this.productService.updateLanguageProduct(this.modelForm));
      this.IComMessageService.showAlertMessageDone('Edit language success');
      this.handlerCancel();
      this.loadPage();
    } catch (err) {
      this.IComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message);
    } finally {
      this.isLoading = false;
      this.cdr.detectChanges();
    }
  }

  protected handlerEdit(e: Event, row: IProductLanguage) {
    e.stopPropagation();
    this.modelForm = row;
    this.isAddNew = true;
  }

  protected handlerRemove(e: Event, row: IProductLanguage) {
    e.stopPropagation();
    this.modalService.confirm({
      nzTitle: 'Are you sure delete this product?',
      nzContent: `<b style="color: red;">${row.name}</b>`,
      nzOkText: 'Delete',
      nzOkType: 'primary',
      nzOkDanger: true,
      nzMaskClosable: false,
      nzClosable: false,
      nzOnOk: this.processDelete.bind(this, row),
      nzCancelText: 'Cancel'
    }, 'error');
  }

  private processDelete(row: IProductLanguage) {
    return  lastValueFrom(this.productService.deleteProductLanguage(row.id || '')).then(() => {
      this.IComMessageService.showAlertMessageDone('Delete language success');
      this.loadPage();
    }).catch((err) => {
      this.IComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message);
    }) 
  }
}
