/* eslint-disable @typescript-eslint/no-explicit-any */
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit, inject } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzFormModule } from 'ng-zorro-antd/form';
import { NgForOf, NgIf, NgSwitch, NgSwitchCase } from '@angular/common';
import { NzTypographyModule } from 'ng-zorro-antd/typography';
import { NzPageHeaderModule } from 'ng-zorro-antd/page-header';
import { NzSpaceModule } from 'ng-zorro-antd/space';
import { NzTabsModule } from 'ng-zorro-antd/tabs';
import { NzIconModule } from 'ng-zorro-antd/icon';
import { IComLibsServicesProductService } from "@icom/services/projects";
import { ActivatedRoute } from '@angular/router';
import { IProduct } from '@icom/interfaces';
import { IcomLibsProductDetailGeneralComponent } from './general/general.component';
import { IcomLibsProductDetailPriceComponent } from './price/price.component';
import { IcomLibsProductDetailPropertyComponent } from './properties/properties.component';
import { IcomLibsProductDetailLanguageComponent } from './language/language.component';
import { IcomLibsProductDetailMediaComponent } from './media/media.component';
import { IcomLibsProductDetailInventoryComponent } from './inventory/inventory.component';
import { IcomLibsProductDetailDiscountComponent } from './discount/discount.component';
import { lastValueFrom } from 'rxjs';
import { IcomLibInventoryListItemsComponent } from '@icom/inventory/items';
import { cloneDeep } from 'lodash';
@Component({
  selector: 'icom-libs-product-detail',
  templateUrl: './app.component.html',
  standalone: true,
  changeDetection: ChangeDetectionStrategy.OnPush,
  imports: [
    NgxDatatableModule,
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NzFormModule,
    NgForOf,
    NgSwitch,
    NgSwitchCase,
    NzTypographyModule,
    NzPageHeaderModule,
    NzSpaceModule,
    NzTabsModule,
    NzIconModule,
    IcomLibsProductDetailGeneralComponent,
    IcomLibsProductDetailPriceComponent,
    NgIf,
    IcomLibsProductDetailPropertyComponent,
    IcomLibsProductDetailLanguageComponent,
    IcomLibsProductDetailMediaComponent,
    IcomLibsProductDetailInventoryComponent,
    IcomLibsProductDetailDiscountComponent,
    IcomLibInventoryListItemsComponent
  ],
})

export class IcomLibsProductDetailComponent implements OnInit {
  protected product: IProduct | undefined;
  protected productTemp: IProduct | undefined;
  protected productId: number;
  protected productService = inject(IComLibsServicesProductService);
  protected cdr = inject(ChangeDetectorRef);

  constructor(private routeActive: ActivatedRoute) {
    this.productId = this.routeActive.snapshot.params['id'];
  }

  async ngOnInit() {
    try {
      this.product = await lastValueFrom(this.productService.getProduct(this.productId));
      this.productTemp = cloneDeep(this.product);
      this.cdr.detectChanges();
    } catch(err) {
      console.log(err);
    }
  }
}
