import { TemplateRef } from "@angular/core"
import { TYPE_TEMPLATE_REF } from "@icom/interfaces"
import { IComLibsServicesInventoryService } from "@icom/services/projects";
import { FormlyFieldConfig } from "@ngx-formly/core";
import { map } from "rxjs";

export const ConfigColumnInventory = () => {
  return [
    { prop: 'name', sortable: false },
    { prop: 'type', sortable: false },
    { prop: 'sold_stock', sortable: false, name: 'Quantity Sold' },
    { prop: 'in_stock', sortable: false, name: 'Quantity Available' },
    { prop: 'total_stock', sortable: false, name: 'Quantity Total' },
  ]
};


export const ConfigFormlyInventory = (iComLibsServicesInventoryService: IComLibsServicesInventoryService): FormlyFieldConfig[] => [
  {
    key: 'inventory_id',
    type: 'select-custom',
    templateOptions: {
      label: 'Inventory',
      placeholder: 'Select Inventory',
      viewValue: 'name',
      labelProp: 'name',
      required: true,
      valueProp: 'id',
      options: iComLibsServicesInventoryService.searchInventory().pipe(
        map(result => {
          return result?.data?.items;
        }
        ))
    },
  }
];